
















































































































































































































import { Component, Vue, Watch } from 'vue-property-decorator'
import ResizableView from '../../../components/utility/ResizableView.vue'
import {
  readOperationsOneCustomer,
  readOperationsOneSession,
  readOperationsActivities,
} from '../../../store/operations/getters'

@Component({
  components: {
    ResizableView,
  },
})
export default class Activity extends Vue {
  public headers = [
    {
      text: 'Session ID',
      sortable: true,
      value: 'sessionId',
      align: 'center',
    },
    {
      text: 'Time',
      sortable: true,
      value: 'time',
      align: 'center',
    },
    {
      text: 'Activity',
      sortable: true,
      value: 'action',
      align: 'center',
    },
    {
      text: 'Activity Details',
      sortable: true,
      value: 'activityDetails',
      align: 'center',
    },
    {
      text: 'Reference ID',
      sortable: true,
      value: 'actionReference',
      align: 'center',
    },
    {
      text: 'Affect on Funds',
      sortable: true,
      value: 'affectOnFunds',
      align: 'center',
    },
    {
      text: 'Available Funds',
      sortable: true,
      value: 'availableFunds',
      align: 'center',
    },
  ]

  public sessionId: string = this.$route.params.sessionId || ''
  public searchByEmail = this.$route.params.searchByEmail || ''
  public searchedFields: any = this.$route.params.searchedFields || {}
  public sessionActivities = this.activitiesBySessionId

  @Watch('selectedCustomer')
  public setSessions() {
    this.selectedCustomer
  }

  get selectedCustomer() {
    return readOperationsOneCustomer(this.$store)
  }

  // public async mounted() {
  //   return readOperationsOneCustomer(this.$store)
  // }

  get activitiesBySessionId() {
    const activities = readOperationsOneSession(this.$store)
    return activities
  }

  get filteredActivities() {
    const activities = readOperationsActivities(this.$store)
    const filtered = activities.filter((activity) => activity.sessionId === this.sessionId)
    return filtered
  }

  public consolelog() {
    console.log('this.$route.params: ', this.$route.params)
    console.log('sessionId: ', this.sessionId)
  }

  public async logout() {
    // await dispatchUserLogOut(this.$store, this.$auth);
    console.log('clicked logout...')
  }
}
